//
// Buttons
//

.btn-bd-download-killer,
.btn-bd-primary-killer {
  font-weight: 600;
  color: $bd-download;
  border-color: $bd-download;

  &:hover,
  &:active {
    color: $white;
    background-color: $bd-download;
    border-color: $bd-download;
  }

  &:focus {
    box-shadow: 0 0 0 3px rgba($bd-download, .25);
  }
}

.btn-clipboard-killer {
  position: absolute;
  top: .65rem;
  right: .65rem;
  z-index: 10;
  display: block;
  padding: .25rem .5rem;
  font-size: .65em;
  color: $orange;
  background-color: $white;
  border: 1px solid;
  border-radius: .25rem;
  &:hover,
  &:active {
    color: $white;
    background-color: $orange;
    border-color: $orange;
  }
  &:focus {
    box-shadow: 0 0 0 3px rgba($orange, .25);
  }
}

// Titles
.bd-title {
  color: $primary;
}
.bd-lead{
  color: $secondary;
